
.todo-alert {
    border: 2px solid #17a2b8;
    background-color: #d4ecf9;
    color: #0c5460;
    padding: 10px;
    border-radius: 5px;
    margin: 10px 0;
    transition: background-color 0.3s ease; /* Add a smooth transition effect */
  
    /* Add hover styles */
    &:hover {
      background-color: #17a2b8;
      color: #fff;
    }
  }