/* Main.css */
@import url('https://fonts.googleapis.com/css2?family=Playpen+Sans&display=swap');


.main {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100vh;
    background-color: #f0f0f0;
    color: blueviolet;
    background-color: rgb(231, 241, 204);
  }
  
  .mainHeading {
    font-family: 'Playpen Sans', sans-serif;
    color: blueviolet;
    font-size: 24px;
    margin: 0;
    font-size: 200%;
    margin-left: 5%;
    margin-right: 5%;
  }
  
  .box {
    color: blueviolet;
    background-color: aquamarine;
    margin-top: 20px;
    background-color: #fff;
    border: 1px solid #ccc;
    border-radius: 8px;
    padding: 16px;

  }
  
  .btn {
    margin-top: 20px;
    background-color: #6773C3;
    color: #fff;
    border: none;
    padding: 10px 20px;
    font-family: 'Playpen Sans', sans-serif;
    cursor: pointer;
  }
  
  .btn:hover {
    background-color: #4A4FAF;
  }
  